<template>
  <Modal
    :active="showModal"
    :event-name="'guidelines-and-email-template-modal'"
    :full-screen="'nexus'"
    :custom-width="
      type === 'ATTACHED_FILES'
        ? 'w-full ipad:w-[768px]'
        : 'w-full ipad:w-[90dvw] ipadPro:w-[1024px] lg:w-[1280px]'
    "
    :custom-horizontal-padding="
      type === 'ATTACHED_FILES' ? 'px-spacing-0' : undefined
    "
  >
    <IconX
      @click.native="handleClose()"
      class="absolute right-spacing-300 top-spacing-300 h-spacing-300 w-spacing-300 cursor-pointer text-content-on-neutral-secondary hover:text-content-on-neutral-primary"
    />
    <div
      v-if="type !== 'ATTACHED_FILES'"
      class="disabled flex w-full flex-col gap-spacing-300 ipad:flex-row ipad:items-stretch"
    >
      <div
        class="flex w-full flex-col gap-spacing-100 ipad:w-[calc(50%-0.75rem)]"
      >
        <p
          class="text-center text-body/l/relaxed/500 text-content-on-neutral-secondary"
        >
          Guidelines
        </p>
        <TextEditor
          :value="marketResearchData?.guidelines"
          :view-only="true"
          label="Guide Lines"
          editor-id="guidelines"
          class="border border-stroke-medium ipad:max-h-[85dvh] ipad:overflow-y-auto"
        />
      </div>
      <div
        class="flex w-full flex-col gap-spacing-100 ipad:w-[calc(50%-0.75rem)]"
      >
        <p
          class="text-center text-body/l/relaxed/500 text-content-on-neutral-secondary"
        >
          Email Template
        </p>
        <TextEditor
          :value="marketResearchData?.emailTemplate"
          :view-only="true"
          label="Email Template"
          editor-id="emailTemplate"
          class="border border-stroke-medium ipad:max-h-[85dvh] ipad:overflow-y-auto"
        />
      </div>
    </div>
    <div v-else class="min-h-[400px]">
      <GeneralForm
        :heading="'ATTACHED FILES'"
        :sub-heading="marketResearchData?.name"
        :show-submit-button="false"
        @close-pressed="handleClose"
        class="flex-1"
      >
        <template #form-body>
          <ClassicTable
            :headers="fileHeaders"
            :horizontal-padding="'px-spacing-0'"
            :bgClass="'bg-transparent'"
          >
            <template #table-body>
              <tr
                v-for="(file, index) in marketResearchData?.relatedFiles"
                class="border-b border-stroke-medium text-body/m/relaxed/400 text-content-on-neutral-primary"
                :key="index"
              >
                <td class="py-spacing-100 pr-spacing-100">
                  {{ file?.split("/")?.pop() }}
                </td>
                <td class="py-spacing-100 pr-spacing-100">
                  <DownloadAndView :file="file" />
                </td>
              </tr>
            </template>
          </ClassicTable>
        </template>
      </GeneralForm>
    </div>
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
import TextEditor from "../General/TextEditor.vue";
export default {
  components: {
    Modal,
    TextEditor,
    GeneralForm: () => import("@/components/General/Form.vue"),
    ClassicTable: () => import("@/components/General/Table/ClassicTable.vue"),
    IconX: () => import("@/components/svgIcons/x.vue"),
    DownloadAndView: () => import("@/components/Quotation/DownloadAndView.vue"),
  },
  data() {
    return {
      showModal: false,
      marketResearchData: null,
      type: "GUIDELINES_AND_TEMPLATES_PREVIEW",
      fileHeaders: [
        {
          key: "fileName",
          value: "File Name",
          sortable: false,
        },
        {
          key: "action",
          value: "Action",
          sortable: false,
        },
      ],
    };
  },
  mounted() {
    this.$bus.$on("guidelines-and-email-template-modal", (data) => {
      if (data?.showModal) {
        this.showModal = data?.showModal;
        this.marketResearchData = data?.marketResearchData;
        if (data?.type) {
          this.type = data?.type;
        }
      } else {
        this.showModal = false;
        this.marketResearchData = null;
        this.type = "GUIDELINES_AND_TEMPLATES_PREVIEW";
      }
    });
  },
  methods: {
    handleClose() {
      this.$bus.$emit("guidelines-and-email-template-modal", false);
    },
  },
  beforeDestroy() {
    this.$bus.$off("guidelines-and-email-template-modal");
  },
};
</script>
